@media only screen and (max-width: 767px) { .our-team__content { padding-top: 24px; flex-direction: column; } }

.our-team__image { width: 55%; height: auto; }

@media only screen and (max-width: 767px) { .our-team__image { width: 100%; } }

.our-team__content-text { align-self: stretch; width: 45%; max-width: 385px; margin-left: 40px; }

@media only screen and (max-width: 767px) { .our-team__content-text { width: 100%; padding: 0 12px; margin-top: 12px; margin-left: 0; max-width: none; } }

.our-team__content-title { font-weight: 400; letter-spacing: 0.1px; }

.our-team__content-desc { font-size: 16px; color: #555555; line-height: 1.625; margin-top: 16px; letter-spacing: 0.4px; }

.company-vision { background-color: #f8fdff; color: #283238; }

.company-vision__wrapper { padding: 80px 27px; }

.company-vision__title { text-align: center; font-weight: 400; }

.company-vision__list { display: flex; margin-top: 40px; justify-content: space-between; }

@media only screen and (max-width: 767px) { .company-vision__list { flex-wrap: wrap; } }

.company-vision__item { display: flex; flex-direction: column; align-items: center; width: 25%; max-width: 224px; }

@media only screen and (max-width: 767px) { .company-vision__item { padding: 0 6px; max-width: none; width: 50%; } }

@media only screen and (max-width: 399px) { .company-vision__item { width: 100%; } }

.company-vision__item-title { font-family: 'Noto Sans', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif; font-size: 16px; font-weight: 700; text-align: center; }

.company-vision__item-text { font-family: 'Noto Sans', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif; margin-top: 16px; font-size: 14px; letter-spacing: 0.5px; color: #333; text-align: center; }

.team-photoset { background-color: #f8fdff; }

.team-photoset__wrapper { padding-right: 48px; }

@media only screen and (max-width: 767px) { .team-photoset__wrapper { padding: 0 24px; } }

.team-photoset__row { display: flex; flex-direction: row-reverse; flex: 0 0 0; }

@media only screen and (max-width: 767px) { .team-photoset__row { flex-wrap: wrap; } }

.team-photoset__row_top { align-items: flex-end; }

.team-photoset__row_bottom { margin-top: 16px; }

.team-photoset__image { object-fit: cover; border-radius: 6px; margin-left: 16px; }

.team-photoset__image_first { width: 20.25%; height: 258px; }

@media only screen and (max-width: 767px) { .team-photoset__image_first { object-position: right; width: 37%; } }

.team-photoset__image_second { width: 46.19%; height: 387px; }

@media only screen and (max-width: 767px) { .team-photoset__image_second { margin-top: 16px; width: 100%; order: 1; } }

.team-photoset__image_third { width: 19.82%; height: 276px; }

@media only screen and (max-width: 767px) { .team-photoset__image_third { margin-left: 0; width: 57%; } }

.team-photoset__image_fourth { width: 15.8%; height: 282px; }

@media only screen and (max-width: 767px) { .team-photoset__image_fourth { width: 47%; } }

.team-photoset__image_fifth { width: 17.95%; height: 282px; }

@media only screen and (max-width: 767px) { .team-photoset__image_fifth { margin-left: 0; width: 47%; } }

.team-photoset__image_sixth { width: 45.76%; height: 387px; }

@media only screen and (max-width: 767px) { .team-photoset__image_sixth { width: 64%; margin-top: 16px; } }

.team-photoset__image_seventh { width: 17.02%; height: 330px; }

@media only screen and (max-width: 767px) { .team-photoset__image_seventh { object-position: left; width: 30%; margin-left: 0; margin-top: 16px; } }

.team-cards { background-color: #f8fdff; padding-top: 56px; }

.team-cards__wrapper { padding: 0 26px; }

.team-cards__list { display: flex; flex-wrap: wrap; margin-left: -23px; }

.team-cards__item { width: calc(25% - 23px); height: 286px; margin-top: 24px; border-radius: 4px; margin-left: 23px; }

@media only screen and (max-width: 767px) { .team-cards__item { width: calc(50% - 23px); min-width: 158px; } }

@media only screen and (max-width: 408px) { .team-cards__item { width: calc(100% - 23px); } }

.team-cards__team { overflow: hidden; position: relative; padding: 0 24px 16px; display: flex; box-shadow: 0 10px 10px 0 rgba(135, 152, 164, 0.19); }

@media only screen and (max-width: 1023px) { .team-cards__team { padding: 0 8px 12px; } }

.team-cards__team-icon { position: absolute; }

.team-cards__team-icon_green { top: -8px; left: -64px; }

.team-cards__team-icon_red { top: -38px; right: -38px; }

.team-cards__team-icon_blue { right: -24px; top: -48px; }

.team-cards__team_green { background: linear-gradient(141deg, #23bcba, #45e994); }

.team-cards__team_red { background: linear-gradient(141deg, #ffdea7, #ea6362); }

.team-cards__team_blue { background: linear-gradient(141deg, #4661ff, #2e9fff); }

.team-cards__team-name { font-size: 34px; color: #fff; margin-top: auto; }

@media only screen and (max-width: 1023px) { .team-cards__team-name { font-size: 28px; } }

.team-cards__person { display: flex; flex-direction: column; align-items: center; box-shadow: 0 -10px 30px 0 rgba(135, 152, 164, 0.15), 0 10px 30px 0 rgba(135, 152, 164, 0.15); background-color: #fff; padding: 40px 12px 24px; }

@media only screen and (max-width: 1023px) { .team-cards__person { padding: 24px 12px 24px; } }

.team-cards__person-image { width: 120px; height: 120px; object-fit: cover; border-radius: 50%; }

.team-cards__person-name { font-family: 'Noto Sans', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif; font-size: 16px; font-weight: 700; line-height: 1.625; margin-top: 26px; text-align: center; }

@media only screen and (max-width: 1023px) { .team-cards__person-name { margin-top: 20px; } }

.team-cards__person-position { font-family: 'Noto Sans', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif; font-size: 14px; letter-spacing: 0.5px; color: #1a6292; margin-top: 11px; text-align: center; }

@media only screen and (max-width: 1023px) { .team-cards__person-position { margin-top: 8px; } }
